
.list {
  margin-top: -20px;
}
.list .weui-cells:before {
  display: block;
}
.list .list-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  padding-right: 15px;
}
.list .list-wrap .list-img {
  width: 1.6rem;
  height: 1.6rem;
  margin-right: 15px;
}
.list .list-wrap .list-content {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.list .list-wrap .list-content h3 {
  font-weight: 400;
  font-size: .36rem;
}
.list .list-wrap .list-content .content-font {
  font-size: .32rem;
  line-height: .42rem;
  color: #999;
}
.list .list-wrap .list-content .view-and-comment {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  color: #999;
}
.list .list-wrap .list-content .view-and-comment .view,
.list .list-wrap .list-content .view-and-comment .comment {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: .32rem;
  margin-right: 15px;
}
.list .list-wrap .list-content .view-and-comment .icon-view,
.list .list-wrap .list-content .view-and-comment .icon-comment {
  width: .4rem;
  height: .4rem;
  margin-right: 4px;
}



