
body {
  background-color: #f7f7f7;
}
.swiper-img img {
  width: 100%;
  height: 100%;
}
.content {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 10px 12px 30px;
  background-color: #fff;
}
.content.content_2 {
  margin-top: 15px;
}
.content.content_3 {
  margin-top: 15px;
}
.content img {
  width: 98%;
  margin: 10px auto;
}
.content h3 {
  font-weight: normal;
  font-size: .36rem;
  line-height: .72rem;
  padding-left: 10px;
}
.content p {
  font-size: .32rem;
  color: #929292;
  padding: 0 5px;
}
.message {
  width: 100%;
  height: 1.2rem;
  background: #359dda;
  text-align: center;
  line-height: 1.2rem;
  color: #fff;
  font-size: .36rem;
}
.message img {
  width: .36rem;
  height: .32rem;
}



