
html,
body {
  background: #fff;
}
.swiper-img img {
  width: 100%;
  height: 100%;
}
.title {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px 0 20px;
  background: #fff;
}
.title span {
  width: .5rem;
  height: 1px;
  display: inline-block;
  background: #000;
}
.title h2 {
  font-weight: normal;
  font-size: .44rem;
  margin: 0 20px;
  letter-spacing: 4px;
}



