
html,
body {
  background: #fff;
}
html .weui-cells,
body .weui-cells {
  font-size: .32rem;
  margin-top: 15px;
}
html .weui-cells .weui-cell,
body .weui-cells .weui-cell {
  padding: 15px 15px;
}
html .weui-cells:before,
body .weui-cells:before {
  display: none;
}
html .submit,
body .submit {
  width: 3.2rem;
  height: .7rem;
  background: #359dda;
  border-radius: 5px;
  text-align: center;
  line-height: .7rem;
  color: #fff;
  margin: 4rem auto 0;
  font-size: .32rem;
}



